import React, { Component } from 'react'
import { Card, Select, Input, Button, Icon, Table, message } from 'antd'
import LinkButton from '../../components/link-button'
import { reqProducts, reqSearchProducts, reqUpdateStatus } from '../../api'
import { PAGE_SIZE } from '../../utils/constants'
// import memoryUtils from '../../utils/memoryUtils'
import { connect } from 'react-redux'
import { addProduct } from '../../redux/actions'

const { Option } = Select

class ProductHome extends Component {
  state = {// 
    total: 0,
    products: [],
    loading: false, // 
    searchName: '', // 
    searchType: 'productName' // 
  }
  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'desc',
      },
      {
        width: 100,
        title: '',
        dataIndex: 'price',
        render: (price) => '' + price // ,
      },
      {
        width: 100,
        title: '',
        // dataIndex: 'status',
        render: (product) => {
          const { status, _id } = product
          const newStatus = status === 1 ? 2 : 1
          return (
            <span>
              <Button
                type='primary'
                onClick={() => this.updateStatus(_id, newStatus)}
              >
                {status === 1 ? '' : ''}
              </Button>
              <span>{status === 1 ? '' : ''}</span>
            </span>
          )
        }
      },
      {
        width: 100,
        title: '',
        render: (product) => {
          return (
            <span>
              <LinkButton onClick={() => this.showDetail(product)}></LinkButton>
              <LinkButton onClick={() => this.showUpdate(product)}></LinkButton>
            </span>
          )
        }
      },
    ]
  }


  /*
  
  */
  showDetail = (product) => {
    // productdetail
    // memoryUtils.product = product
    this.props.addProduct(product) // actionproductstore
    this.props.history.push('/product/detail') // 
  }


  /*
  
  */
  showUpdate = (product) => {
    // memoryUtils.product = product // productmemoryUtils
    this.props.addProduct(product) // productredux
    this.props.history.push('/product/addupdate') //
  }

  // 
  getProducts = async (pageNum) => {
    this.pageNum = pageNum //pageNum,
    this.setState({ loading: true })//loading
    const { searchName, searchType } = this.state
    let result
    if (searchName) {
      result = await reqSearchProducts({ pageNum, pageSize: PAGE_SIZE, searchName, searchType })
    } else {
      result = await reqProducts(pageNum, PAGE_SIZE)
    }
    this.setState({ loading: false })//loading
    if (result.status === 0) {//
      // 
      const { total, list } = result.data
      // 
      this.setState({ total, products: list })
    } else {
      message.error('')
    }
  }

  updateStatus = async (productId, status) => {
    const result = await reqUpdateStatus(productId, status)
    if (result.status === 0) {
      message.success('')
      this.getProducts(this.pageNum)
    } else {
      message.error('')
    }
  }

  componentWillMount() {
    this.initColumns()
  }
  componentDidMount() {
    this.getProducts(1)
  }

  render() {
    const { products, total, loading, searchName, searchType } = this.state
    const title = (
      <span>
        <Select value={searchType} style={{ width: 150 }} onChange={value => this.setState({ searchType: value })}>{/**/}
          <Option value='productName'></Option>
          <Option value='productDesc'></Option>
        </Select>
        <Input placeholder='' value={searchName} style={{ width: 150, margin: '0 15px' }} onChange={event => this.setState({ searchName: event.target.value })}></Input>{/**/}
        <Button type='primary' onClick={() => this.getProducts(1)}></Button>
      </span>
    )
    const extra = (
      <Button type='primary' onClick={() => this.props.history.push('/product/addupdate')}>
        <Icon type='plus'></Icon>
        
      </Button>
    )
    return (
      <Card title={title} extra={extra}>
        <Table
          rowKey='_id'
          dataSource={products}
          columns={this.columns}
          bordered
          loading={loading}
          pagination={
            {
              current: this.pageNum,
              total,//
              defaultPageSize: PAGE_SIZE,//
              showQuickJumper: true,//
              onChange: this.getProducts//
            }
          }
        />
      </Card>
    )
  }
}

export default connect(
  state => ({ product: state.product }),
  { addProduct }
)(ProductHome)