/*
ajax
axios
promise
1. 1:?
    :Promise
    reject(error)(antdmessage)
2. 2: responseresult:resolve(response.data)    
*/


/*
43urltypedata()4
*/
import axios from 'axios'
import { message } from 'antd'

export default function ajax(url, data = {}, type = 'GET') {

  return new Promise((resolve, reject) => {
    let promise
    // 1. ajax
    if (type === 'GET') { // get
      promise = axios.get(url, { // 
        params: data // 
      }).then()
    } else { // post
      promise = axios.post(url, data)
    }

    // 2. resolve(value)
    promise.then(response => {
      // responseresult:resolve(response.data),result
      resolve(response.data)
    })

      // 3. reject(reason)
      .catch(error => {
        message.error(':' + error.message)
      })
  })

}

