import React, { Component } from 'react'
import { Card, Button, Table, Modal, message } from 'antd'
import LinkButton from '../../components/link-button'
import { PAGE_SIZE } from '../../utils/constants'
import formateDate from '../../utils/formateDate'
import { reqUsers, reqDeleteUser, reqAddOrUpdateUser } from '../../api'
import UserForm from './user-form'

const { confirm } = Modal

/**

*/
export default class User extends Component {
  state = {
    users: [], // 
    roles: [], // 
    isShow: false // 
  }

  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'username'
      },
      {
        title: '',
        dataIndex: 'email'
      },
      {
        title: '',
        dataIndex: 'phone'
      },
      {
        title: '',
        dataIndex: 'create_time',
        render: formateDate
      },
      {
        title: '',
        dataIndex: 'role_id',
        // render: (role_id)=>this.state.roles.find(role=>role._id===role_id).name // 
        render: (role_id) => this.roleNames[role_id] // 
      },
      {
        title: '',
        render: (user) => ( // user:user
          <span>
            <LinkButton onClick={() => this.showUpdate(user)}></LinkButton>
            <LinkButton onClick={() => this.deleteUser(user)}></LinkButton>
          </span>
        )
      },
    ]
  }

  /*
  
  */
  addOrUpdateUser = async () => {
    // 
    this.setState({ isShow: false })
    // 1. 
    const user = this.form.getFieldsValue()
    // 
    this.form.resetFields()

    // user_id
    if (this.user && this.user._id) {
      // user_id
      user._id = this.user._id
    }

    // 2.   
    const result = await reqAddOrUpdateUser(user)
    // 3. 
    if (result.status === 0) {
      message.success(`${this.user ? '' : ''}`)
      this.getUsers()
    }
  }

  /*
  role(id)
  */
  initRoleNames = (roles) => { //initRoleNames(roles)roleNamesrole._idnamekeyrole._idvalueroleNamesthis
    const roleNames = roles.reduce((pre, role) => { // roles=>roleNames
      pre[role._id] = role.name
      return pre
    }, {})
    // roleNamesthis
    this.roleNames = roleNames
  }

  getUsers = async () => {
    const result = await reqUsers()
    if (result.status === 0) {
      // message.success('!')
      const { users, roles } = result.data
      this.initRoleNames(roles) // userrolesinitRoleNames(roles)role._idname
      this.setState({
        users,
        roles
      })
    } else {
      message.error('!')
    }
  }

  // 
  deleteUser = (user) => {
    confirm({
      title: `${user.username}?`,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const result = await reqDeleteUser(user._id)
        if (result.status === 0) {
          message.success('!')
          this.getUsers() //getUsers()
        } else {
          message.error('!')
        }
      },
      // onCancel() {
      //   console.log('Cancel');
      // },
    });
  }

  /*
  
  */
  showAdd = () =>{
    // this.userthis.user
    this.user = null
    // 
    this.setState({ isShow: true })
  }

  /*
  
  */
  showUpdate = (user) => {
    this.user = user
    this.setState({ isShow: true })
  }

  componentWillMount() {
    this.initColumns()
  }

  componentDidMount() {
    this.getUsers()
  }

  handleCancel = () =>{
    this.setState({ isShow: false })
    // 
    this.form.resetFields()
  }

  render() {
    const { users, isShow, roles } = this.state
    const user = this.user || {}
    const title = (
      <Button type='primary' onClick={this.showAdd}></Button>
    )
    return (
      <Card title={title}>
        <Table
          dataSource={users}
          columns={this.columns}
          bordered={true}
          rowKey='_id'
          pagination={{ defaultPageSize: PAGE_SIZE, showQuickJumper: true }} // 5,
        />
        <Modal
          title={user._id ? '' : ''}
          visible={isShow}
          onOk={this.addOrUpdateUser}
          onCancel={this.handleCancel}
        >
          <UserForm setForm={form => this.form = form} roles={roles} user={user} />
        </Modal>
      </Card>
    )
  }
}
