import React, { PureComponent } from 'react'
import { Form, Input, Select } from 'antd'
import PropTypes from 'prop-types'
const Item = Form.Item
const Option = Select.Option

/*
/form
*/
class UserForm extends PureComponent {

  static propTypes = {
    setForm: PropTypes.func.isRequired, //form
    roles: PropTypes.array.isRequired,
    user: PropTypes.object
  }

  // 
  validatorPwd = (rule, value, callback) => {
    if (!value) {
      callback('')
    } else if (value.length < 4) {
      callback('')
    } else if (value.length > 12) {
      callback('')
    } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
      callback('')
    } else {
      callback()
    }
  }

  componentWillMount() {
    this.props.setForm(this.props.form)
  }
  render() {
    const { getFieldDecorator } = this.props.form
    const formItemLayout = {// Item
      labelCol: { span: 4 }, // label
      wrapperCol: { span: 15 },//
    };
    const { roles, user } = this.props
    // console.log(user)
    return (
      <Form {...formItemLayout}>
        <Item label=''>
          {
            getFieldDecorator('username', {
              initialValue: user.username,
              rules: [
                { required: true, whitespace: true, message: '' },
                { min: 4, message: '' },
                { max: 12, message: '' },
                { pattern: /^[a-zA-Z][a-zA-Z0-9_]*$/, message: '' }
              ]
            })(
              <Input placeholder='' />
            )
          }
        </Item>
        {user._id ? null : (<Item label=''>
          {
            getFieldDecorator('password', {
              initialValue: '',
              rules: [{ required: true }, { validator: this.validatorPwd }],
            })(
              <Input placeholder='' type='password' />
            )
          }
        </Item>)}
        <Item label=''>
          {
            getFieldDecorator('phone', {
              initialValue: user.phone,
              rules: [
                { required: true, message: '' },
                { pattern: /^1[3456789]\d{9}$/, message: '' }
              ]
            })(
              <Input placeholder='' type='tel' />
            )
          }
        </Item>
        <Item label=''>
          {
            getFieldDecorator('email', {
              initialValue: user.email,
              rules: [
                { required: true, message: '' },
                { pattern: /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/, message: '' }
              ]
            })(
              <Input placeholder='' />
            )
          }
        </Item>
        <Item label=''>
          {
            getFieldDecorator('role_id', {
              initialValue: user.role_id,
            })(
              <Select>
                {
                  roles.map(role => (
                    <Option value={role._id} key={role._id}>{role.name}</Option>
                  ))
                }
              </Select>
            )
          }
        </Item>
      </Form>
    )
  }
}
const WrapAddForm = Form.create()(UserForm)
export default WrapAddForm
