import React, { Component } from 'react'
import { Card, Icon, Button, Table, message, Modal } from 'antd'
import LinkButton from '../../components/link-button'
import { reqCategorys, reqUpdateCategory, reqAddCategory } from '../../api'
import AddForm from './add-form'
import UpdateForm from './update-form'
import {PAGE_SIZE} from '../../utils/constants'


/*

*/

export default class Category extends Component {
  state = {
    categorys: [], // 
    subCategorys: [], // 
    loading: false, // 
    parentId: '0', // parentId
    parentName: '', // 
    showStatus: 0 // /,012:
  }

  /*
  Table
  */
  initcolumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name'
      },
      {
        title: '',
        width: 300,
        render: (category) => (
          <span>
            <LinkButton onClick={() => this.showUpdate(category)}></LinkButton>
            {/* :() */}
            {this.state.parentId === '0' ? (<LinkButton onClick={() => this.showSubCategorys(category)}></LinkButton>) : null}
          </span>
        )
      }
    ]
  }

  ///  
  // parentId(this.state)parentId,parentId
  getCategorys = async (parentId) => {
    // loading
    this.setState({ loading: true })
    parentId = parentId || this.state.parentId
    // ajax
    const result = await reqCategorys(parentId)
    // ,loading
    this.setState({ loading: false })
    if (result.status === 0) {
      // ()
      const categorys = result.data
      if (parentId === '0') { // 
        // 
        this.setState({ categorys })
      } else { // 
        // 
        this.setState({ subCategorys: categorys })
      }
    } else {
      message.error('')
    }
  }

  /*
  
  */
  showSubCategorys = (category) => {
    // 
    this.setState({
      parentId: category._id,
      parentName: category.name
    }, () => { // render()
      // 
      this.getCategorys()
      // console.log(this.state.parentId, this.state.parentName)
    })
    // setStatesetState()
  }

  /*
  
  */
  showCategorys = () => {
    // parentId0,parentNamesubCategorys
    this.setState({
      parentId: '0',
      parentName: '',
      subCategorys: []
    })
  }

  /*
  
  */
  handleCancel = () => {
    this.form.resetFields()
    // 
    this.setState({ showStatus: 0 })
  }

  /*
  
  */
  showAdd = () => {
    this.setState({ showStatus: 1 })
  }



  /*
  
  */
  addCategory = () => {
    this.form.validateFields(async (err, values) => {// 
      if (!err) {
        // console.log('addCategory()')
        // 1.
        this.setState({ showStatus: 0 })
        // 2.,
        /*
        : getFieldsValue() getFieldValue(),
        getFieldValue() getFieldValue()
        */
        // const { parentId, categoryName } = this.form.getFieldsValue()
        const { parentId, categoryName } = values // values
        // 
        this.form.resetFields()
        const result = await reqAddCategory(categoryName, parentId)
        if (result.status === 0) {
          // 3. 
          if (parentId === this.state.parentId) {
            // 
            this.getCategorys()
          } else if (parentId === '0') {//
            this.getCategorys('0')
          }
        } else {
          message.error('')
        }
      }
    })
  }


  /*
  
  */
  showUpdate = (category) => {
    // 
    this.category = category
    this.setState({ showStatus: 2 })
  }

  /*
  
  */
  updateCategory = () => {
    // 
    this.form.validateFields(async (err, values) => {
      if (!err) {
        // 1.
        this.setState({ showStatus: 0 })
        // 
        const categoryId = this.category._id
        const { categoryName } = values
        // 
        this.form.resetFields()
        // 2.
        const result = await reqUpdateCategory({ categoryName, categoryId })
        if (result.status === 0) {
          // 3.
          this.getCategorys()
        } else {
          message.error('')
        }
      }
    })

  }


  // render
  componentWillMount() {
    this.initcolumns()
  }

  // ajax
  componentDidMount() {
    this.getCategorys()
  }

  render() {
    // 
    const { categorys, subCategorys, parentId, parentName, loading, showStatus } = this.state
    // 
    const category = this.category || {} // 
    // card
    const title = parentId === '0' ? '' : (
      <span>
        <LinkButton onClick={this.showCategorys}></LinkButton>
        <Icon type="arrow-right" style={{ marginRight: 5 }} />
        <span>{parentName}</span>
      </span>
    )
    const extra = (
      <Button type='primary' onClick={this.showAdd}>
        <Icon type='plus'></Icon>
        
      </Button>
    )
    return (
      <Card title={title} extra={extra}>
        <Table
          dataSource={parentId === '0' ? categorys : subCategorys}
          columns={this.columns}
          bordered={true}
          rowKey='_id'
          pagination={{ defaultPageSize: PAGE_SIZE, showQuickJumper: true }} // 5,
          loading={loading}
        />
        <Modal
          title=""
          visible={showStatus === 1}
          onOk={this.addCategory}
          onCancel={this.handleCancel}
        >
          <AddForm
            categorys={categorys}
            parentId={parentId}
            setForm={form => this.form = form}
          />

        </Modal>
        <Modal
          title=""
          visible={showStatus === 2}
          onOk={this.updateCategory}
          onCancel={this.handleCancel}
        >
          <UpdateForm categoryName={category.name} setForm={(form) => { this.form = form }} />
        </Modal>
      </Card>
    )
  }
}
