import React, { Component } from 'react'
import { Card, Button, Table, Modal, message } from 'antd'
import { PAGE_SIZE } from '../../utils/constants'
import { reqRoles, reqAddRole, reqUpdateRole } from '../../api'
import AddForm from './add-form'
import AuthForm from './auth-form'
import formateDate from '../../utils/formateDate'
import {connect} from 'react-redux'
import {logout} from '../../redux/actions'

/**

 */
class Role extends Component {
  state = {
    roles: [], // 
    role: {}, // role
    isShowAdd: false, // 
    isShowAuth: false // 
  }
  constructor(props) {
    super(props)
    this.auth = React.createRef()
  }
  initColumn = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'create_time',
        render: create_time => formateDate(create_time)
      },
      {
        title: '',
        dataIndex: 'auth_time',
        render: formateDate // renderformateDate
      },
      {
        title: '',
        dataIndex: 'auth_name',
      },
    ]
  }

  onRow = (role) => {
    return {
      onClick: event => {
        // console.log(role)
        this.setState({ role })
      }
    }
  }

  getRoles = async () => {
    const result = await reqRoles()
    if (result.status === 0) {
      const roles = result.data
      this.setState({ roles })
    } else {
      message.error('')
    }
  }


  // 
  addRole = () => {
    // 
    this.form.validateFields(async (err, values) => {
      if (!err) {// 
        this.setState({ isShowAdd: false })//
        // 
        const { roleName } = values
        this.form.resetFields()// 
        // 
        const result = await reqAddRole(roleName)
        if (result.status === 0) {
          message.success('')
          // this.getRoles()
          // 
          const role = result.data
          // roles

          /*state */
          // const roles = this.state.roles
          // roles.push(role)
          // this.setState({ roles })

          /*
          : 
          */
          this.setState(state => ({//(),stateprops
            roles: [...state.roles, role]
          }))
        } else {
          message.error('')
        }
      }
    })
  }

  // 
  updateRole = async () => {
    this.setState({ isShowAuth: false })//
    const role = this.state.role
    const menus = this.auth.current.getMenus()
    role.menus = menus
    role.auth_time = Date.now()
    role.auth_name = this.props.user.username// 
    // 
    const result = await reqUpdateRole(role)
    if (result.status === 0) {
      // 
      if (role._id === this.props.user.role_id) {
        // action
        this.props.logout()
        message.success('')
      } else {
        message.success('')
        this.setState({
          roles: [...this.state.roles]
        })
      }
    } else {
      message.error('')
    }
  }

  componentWillMount() {
    this.initColumn()
  }

  componentDidMount() {
    this.getRoles()
  }

  render() {
    const { roles, role, isShowAdd, isShowAuth } = this.state
    const title = (
      <span>
        <Button type='primary' onClick={() => { this.setState({ isShowAdd: true }) }}></Button> &nbsp;&nbsp;
        <Button type='primary' disabled={!role._id} onClick={() => { this.setState({ isShowAuth: true }) }}></Button>
      </span>
    )
    return (
      <Card title={title}>
        <Table
          bordered
          rowKey='_id'
          dataSource={roles}
          columns={this.columns}
          pagination={{ defaultPageSize: PAGE_SIZE, showQuickJumper: true }}
          rowSelection={{ 
            type: 'radio', 
            selectedRowKeys: [role._id],
            onSelect: (role)=>{ // radio
              this.setState({ role })
            } 
          }}
          onRow={this.onRow}
        />

        <Modal
          title=""
          visible={isShowAdd}
          onOk={this.addRole}
          onCancel={() => { this.form.resetFields(); this.setState({ isShowAdd: false }) }}
        >
          <AddForm
            setForm={form => this.form = form}
          />
        </Modal>

        <Modal
          title=""
          visible={isShowAuth}
          onOk={this.updateRole}
          onCancel={() => { this.setState({ isShowAuth: false }) }}
        >
          <AuthForm role={role} ref={this.auth} />
        </Modal>
      </Card>
    )
  }
}

export default connect(
  state=>({user:state.user}),
  {logout}
)(Role)
