import React, { Component } from 'react'
import { Card, Icon, List, } from 'antd'
import LinkButton from '../../components/link-button'
import { BASE_IMG_URL } from '../../utils/constants'
import { reqCategory } from '../../api'
// import memoryUtils from '../../utils/memoryUtils'
import {connect} from 'react-redux'
import {addProduct} from '../../redux/actions'

const { Item } = List

class ProductDetail extends Component {
  state = {
    cName1: '',// 
    cName2: '' // 
  }

  async componentDidMount() {
    const { pCategoryId, categoryId } = this.props.product //this.props.location.state.product
    if (pCategoryId === 0) {// 
      const result = await reqCategory(categoryId)
      if (result.status === 0) {
        const cName1 = result.data.name
        this.setState({ cName1 })
      }
    } else {// 
      /*await
      // const result1 = await reqCategory(pCategoryId)//
      // const result2 = await reqCategory(categoryId)// 
      // const cName1 = result1.data.name
      // const cName2 = result2.data.name
      */

      // 
      const results = await Promise.all([reqCategory(pCategoryId), reqCategory(categoryId)])
      const cName1 = results[0].data.name
      const cName2 = results[1].data.name
      this.setState({ cName1, cName2 })
    }
  }

  /*
  ,
  */
  componentWillUnmount() {
    // actionproduct
    this.props.addProduct({})
  }

  render() {
    // state
    const { name, desc, price, detail, imgs } = this.props.product
    const { cName1, cName2 } = this.state
    const title = (
      <span>
        <LinkButton >
          <Icon
            type='arrow-left'
            style={{ marginRight: 15, fontSize: 20 }}
            onClick={() => this.props.history.push('/product')}
          />
        </LinkButton>
        <span></span>
      </span>
    )
    return (
      <Card title={title} className='product-detail'>
        <List>
          <Item>
            <span className='left'>:</span>
            <span>{name}</span>
          </Item>
          <Item>
            <span className='left'>:</span>
            <span>{desc}</span>
          </Item>
          <Item>
            <span className='left'>:</span>
            <span>{price}</span>
          </Item>
          <Item>
            <span className='left'>:</span>
            <span>{cName1}{cName2 ? '-->' + cName2 : ''}</span>
          </Item>
          <Item>
            <span className='left'>:</span>
            <span>
              {imgs.map(img => (
                <img src={BASE_IMG_URL + img} key={img} alt="img" className='product-img' />
              ))}
            </span>
          </Item>
          <Item>
            <span className='left'>:</span>
            <span dangerouslySetInnerHTML={{ __html: detail }}></span>
          </Item>
        </List>
      </Card>
    )
  }
}

export default connect(
  state=>({product:state.product}),
  {addProduct}
)(ProductDetail)