import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'
import './index.less'
import logo from '../../assets/images/logo.png'
import menuList from '../../config/menuConfig'
import { Menu, Icon } from 'antd'
import { connect } from 'react-redux'
import { setHeadTitle } from '../../redux/actions'
const { SubMenu } = Menu;
class LeftNav extends Component {

  /*
  item
  */
  hasAuth = (item) => {
    // item
    const { key, isPublic } = item

    const menus = this.props.user.role.menus
    const username = this.props.user.username
    /*
    1. admin
    2. item
    3. item:keymenus
     */
    if (username === 'admin' || isPublic || menus.indexOf(key) !== -1) {
      return true
    } else if (item.children) { // 4. itemitem
      return !!item.children.find(child => menus.indexOf(child.key) !== -1)
    }
  }

  /*reduce() + */
  getMenuNodes = (menuList) => {
    // 
    const path = this.props.location.pathname
    return menuList.reduce((pre, item) => {
      // item,
      if (this.hasAuth(item)) {
        if (item.key === path || path.indexOf(item.key) === 0){
          this.props.setHeadTitle(item.title)
        }
          if (!item.children) {
            // pre<Menu.Item>
            pre.push((
              <Menu.Item key={item.key}>
                <Link to={item.key} onClick={() => this.props.setHeadTitle(item.title)}>
                  <Icon type={item.icon} />
                  <span>{item.title}</span>
                </Link>
              </Menu.Item>
            ))
          } else {
            // Item
            const cItem = item.children.find(cItem => path.indexOf(cItem.key) === 0) // cItem.key/product
            // item
            if (cItem) this.openKey = item.key

            // pre<SubMenu.Item>
            pre.push((
              <SubMenu
                key={item.key}
                title={
                  <span>
                    <Icon type={item.icon} />
                    <span>{item.title}</span>
                  </span>
                }
              >
                {/*  */
                  this.getMenuNodes(item.children)
                }
              </SubMenu>
            ))
          }
      }
      return pre
    }, [])
  }

  // render()
  // render()()
  componentWillMount() {
    this.menuNode = this.getMenuNodes(menuList)
  }

  render() {
    // 
    let path = this.props.location.pathname
    if (path.indexOf('/product') === 0) {// (/product/detail)
      path = '/product' //product/product
    }
    const openKey = this.openKey
    return (
      <div className='left-nav'>
        <Link to='/home' className='left-nav-header'>
          <img src={logo} alt="logo" />
          <h2>react </h2>
        </Link>
        <Menu
          selectedKeys={[path]}
          defaultOpenKeys={[openKey]}
          mode="inline"
          theme="dark"
        >
          {this.menuNode}
        </Menu>
      </div>
    )
  }
}
export default connect(
  state => ({ user:state.user }),
  { setHeadTitle }
)(withRouter(LeftNav))