import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'
import './login.less'
import logo from '../../assets/images/logo.png'
import { Form, Icon, Input, Button } from 'antd';
import { connect } from 'react-redux'
import { login } from '../../redux/actions'
// const Item = Form.Item // import

/* 

*/
class Login extends Component {
  handleSubmit = (event) => {
    // 
    event.preventDefault()

    // 
    this.props.form.validateFields(async (error, values) => {
      //
      if (!error) {
        // ajax(post)
        const { username, password } = values
        // const result = await reqLogin(username, password)
        // if (result.status === 0) {// 
        //   // 
        //   message.success('',.7)

        //   // user
        //   const user = result.data

        //   memoryUtils.user = user // 

        //   storageUtils.saveUser(user) // localStorage
        //   // ,push()replace
        //   this.props.history.replace('/home')
        // } else { // 
        //   // 
        //   message.error(result.msg)
        // }
        // action => 
        this.props.login(username, password)
      } else {
        console.error('')
      }
      /* 
      asyncawait
      1. : 
        1).promisethen()/
        2).() 
      2. await?
        promiseawait: promisepromisevalue
      2. async?
        await()async
      */
    });
    /*
    form
    const form = this.props.form
    const values = form.getFieldsValue()
    console.log(values)
    */

  }

  /*
  
   */

  /* 
 /
 1). 
 2).4
 3).12
 4).
  */
  validatorPwd = (rule, value, callback) => {
    if (!value) {
      callback('')
    } else if (value.length < 4) {
      callback('')
    } else if (value.length > 12) {
      callback('')
    } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
      callback('')
    } else {
      callback()
    }
    // callback('xxxx) // 
  }

  render() {
    const user = this.props.user
    // 
    if (user && user._id) {
      return <Redirect to='/home' />
    }

    const errorMsg = this.props.user.errorMsg

    // form(form)
    const form = this.props.form
    const { getFieldDecorator } = form
    return (
      <div className='login'>
        <header className='login-header'>
          <img src={logo} alt="logo" />
          <h1>React: </h1>
        </header>
        <section className='login-content'>
          <div className={errorMsg?'error-msg show':'error-msg'}>{errorMsg}</div>
          <h2></h2>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item>
              {/* 
                /
                1). 
                2).4
                3).12
                4).
              */}
              {getFieldDecorator('username', {//username
                // 
                rules: [// ()
                  { required: true, whitespace: true, message: '' },
                  { min: 4, message: '' },
                  { max: 12, message: '' },
                  { pattern: /^[a-zA-Z][a-zA-Z0-9_]*$/, message: '' },
                ],
                // 
                initialValue: 'admin'
              })(
                <Input
                  prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('password', {//
                rules: [{ validator: this.validatorPwd }],
              })(
                <Input
                  prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  type="password"
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" className="login-form-button">
                
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
    )
  }
}

